import {
  Box,
  Heading,
  Button,
  ButtonGroup,
  Container,
  SimpleGrid,
  HStack,
  IconButton,
  Text,
  useBreakpointValue,
  useColorModeValue,
} from '@chakra-ui/react'
import * as React from 'react'
import { FiMenu } from 'react-icons/fi'

interface MainProps {
  title: string,
  description?: string,
  items?: { title: string, description: string }[], 
  backgroundColor?: string,
  fontColor?: string,
  children: any,
}

const Section = ({ title, description, items, backgroundColor, fontColor, children }: MainProps) => {
  const props: any = {}
  if (backgroundColor) props['bg'] = backgroundColor;
  if (fontColor) props['color'] = fontColor;
  
  const renderItems = () => {
    if (items === undefined || items.length === 0) {
      return null;
    }    

    return items.map((value) => {
      return (
        <Box mb={7}>
          <Heading fontSize="3xl" fontWeight="semibold" mb={2}>{value.title}</Heading>
          <Text fontSize="xl">{value.description}</Text>
        </Box>      
      );
    });
  }

  return (    
    <Box
      as="section"
      py={{ base: 6, md: 12}}
      { ...props }
    >
      <Container maxW="6xl">
        <Box width={{ base: '100%', md: '75%' }} mb={{ base: 5, md: 12}}>
          <Heading fontSize="5xl" fontWeight="extrabold" mb={{ base: 1, md: 2}}>{title}</Heading>
          {description && <Text fontSize="xl">{description}</Text>}          
        </Box>
        {items && (
          <SimpleGrid columns={{ base: 1, md: 2 }} spacing={10}>
            {renderItems()}
          </SimpleGrid>
        )}
        {children}
      </Container>
    </Box>
  )
}

export default Section;